.title {
  position: relative;
  font-size: 48px;
  line-height: 1.3;
  font-weight: 600;
  text-align: left;
  display: inline-block;
  transition: 0.5s;
  transform-origin: left;
  transform: scaleX(0);

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--main);
    transform-origin: right;
    transition: 0.5s;
  }
  &.aos-animate {
    transform: scale(1);
    &:before {
      transform: scaleX(0);
      transition-delay: 0.5s;
    }
  }

  &.light {
    margin-left: 1rem;
    color: var(--heaven);
  }
}
