.stayhome {
  position: relative;
  padding: 0 6rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  z-index: 2;

  .img {
    overflow: hidden;
    margin: 0 4rem;
    margin-top: -40px;
    height: 40rem;
    .image {
      background-image: url(../../assest/images/about.jpg);
      background-size: cover;
      background-position: center;
      height: 100%;
    }
  }

  &:hover .img .image {
    transform: scale(1.1);
    transition: transform 8s cubic-bezier(0.25, 0.45, 0.45, 0.95);
  }

  .content {
    margin: 4rem 0;

    p {
      margin: 30px 0;
      color: var(--para);
    }
  }
}

@media only screen and (max-width: 600px) {
  .stayhome {
    padding: 0 10px;
    grid-template-columns: 1fr;

    .img {
      margin: 5rem 0 0 !important;
      height: 30rem !important;
      .image {
        height: 100%;
      }
    }
    .content {
      margin: 1rem 0 !important;

      p {
        margin: 30px 0;
        color: var(--para);
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .stayhome {
    padding: 0 10px;
    grid-template-columns: 1fr;

    .img {
      margin: 0 1rem;
      margin-top: 5rem;
      height: 40rem;
      .image {
        height: 100%;
      }
    }
    .content {
      margin: 1rem 1rem;

      p {
        margin: 30px 0;
        color: var(--para);
      }
    }
  }
}
