.apartment {
  padding: 100px 6rem 130px;

  .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .card-list {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;

    .card {
      border: 1px solid #eee;
      overflow: hidden;
      transition: 1s;
      .image {
        position: relative;
        background-position: center;
        background-size: cover;
        height: 25rem;
        width: 100%;

        &::after {
          content: '';
          background: #000;
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          opacity: 0;
          transition: 0.6s;
          transform: scale(0.5);
        }

        .img-footer {
          opacity: 0;
          position: absolute;
          z-index: 2;
          top: 75%;
          left: 5%;
          transform: translate(-50% -50%);
          color: var(--heaven);
          transition: 0.6s;

          h2 {
            font-size: 18px;
            font-weight: 700;
          }

          h6 {
            font-size: 12px;
          }
        }

        .heart {
          position: absolute;
          z-index: 2;
          top: 95%;
          left: 80%;
          border-radius: 4px;
          height: 3rem;
          width: 3rem;
          background: var(--heaven);
          color: var(--para);
          display: flex;
          justify-content: center;
          align-items: center;
          box-shadow: 0 10px 25.5px 4.5px rgba(20, 48, 58, 0.1);
          transition: 0.6s;
        }
      }

      &:hover {
        box-shadow: 0 10px 25.5px 4.5px rgba(20, 48, 58, 0.1);
      }

      &:hover ::after {
        opacity: 30%;
        transform: scale(1);
      }

      &:hover .image .heart {
        background: var(--main);
        color: var(--heaven);
        box-shadow: none;
      }

      &:hover .image .img-footer {
        opacity: 1;
        top: 84%;
      }

      .content {
        width: 100%;
        padding: 1rem;

        h6 {
          color: var(--main);
          font-size: 14px;
          font-weight: 300;
          margin-bottom: 1rem;
        }

        h3 {
          font-size: 18px;
          font-weight: 700;
          cursor: pointer;

          &:hover {
            color: var(--main);
            transition: 0.5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
          }
        }
      }
      .footer-card {
        border-top: 1px solid #eee;
        padding: 0.5rem 1rem;
        ul {
          list-style: none;
          display: flex;
          justify-content: space-between;
          align-items: center;
          li {
            color: #797979;
            text-align: left;
            i {
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1400px) {
  .apartment {
    padding: 100px 2rem 130px;

    .card-list {
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 10px;
    }
  }
}

@media only screen and (max-width: 1000px) {
  .apartment {
    padding: 100px 1rem 130px;

    .ipad {
      grid-template-columns: 1fr 1fr;
      grid-gap: 10px;
    }
  }
}

@media only screen and (max-width: 600px) {
  .apartment {
    padding: 100px 1rem 130px;
    .heading {
      display: block;
    }
    .card-list {
      grid-template-columns: 1fr;
      grid-gap: 10px;
    }
  }
}
