.area-guide-page {
  .content {
    text-align: center;
    color: var(--heaven);

    h1 {
      font-size: 48px;
      font-weight: 600;
    }

    h5 {
      font-size: 30px;
      font-weight: 500;
      padding: 0 0 2rem;
      font-style: italic;
    }
  }
}

@media only screen and (max-width: 600px) {
  .area-guide-page {
    .content {
      h1 {
        font-size: 30px;
        font-weight: 500;
      }
      h5 {
        font-size: 20px;
        font-weight: 300;
      }
    }
  }
}
