.living {
  padding-top: 130px;
  position: relative;
  z-index: 3;
  // overflow: hidden;

  img {
    position: absolute;
    right: 0;
    z-index: -1;
    top: 130px;
  }

  .desc {
    padding: 244px 0px 120px;
    text-align: left;
  }

  ul {
    list-style: none;

    li {
      i {
        margin-right: 8px;
        color: var(--main);
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    width: 50%;
    top: 130px;
    height: 75%;
    background-image: url(../../assest/images/team_member.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 2;
  }

  &::after {
    position: absolute;
    content: '';
    top: 260px;
    right: 0;
    width: 60%;
    height: 70%;
    background-color: #f7f7f7;
    z-index: -1;
  }
}

@media only screen and (max-width: 992px) {
  .living {
    &::before {
      display: none;
    }
  }
}
