.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 6rem 0;

  .search {
    border: 1px solid #f0e9ff;
    width: 100%;
    padding: 1rem;
    background: rgb(252, 252, 252);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.08);
    transition: 0.5s;

    &:focus {
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    }
  }
}

@media only screen and (max-width: 1000px) {
  .search-box {
    padding: 3rem 1rem 0;
  }
}
