.header {
  z-index: 10;
  position: relative;
  .logo {
    color: var(--dark);
  }

  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem 1.5rem;
    transition: 0.5s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 10vh;
    z-index: 1;

    .phone {
      z-index: 2;
      height: 100%;
      width: 0;
      position: fixed;
      top: 0;
      left: 0;
      overflow-x: hidden;
      -webkit-transition: 0.5s;
      transition: 0.5s;
      background: #1b1b1b;
      color: #fff;

      .close-btn {
        position: absolute;
        top: 0;
        right: 22px;
        font-size: 36px;
        margin-left: 50px;
        cursor: pointer;
        z-index: 2;
      }

      ul {
        list-style: none;
        li {
          position: relative;
          color: var(--heaven);
          text-decoration: none;
          font-size: 18px;
          padding-left: 40px;
          font-weight: 500;
          display: block;
          width: 100%;
          border-left: 3px solid transparent;
          line-height: 60px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }
        .logo {
          color: var(--heaven);
          font-size: 40px;
          font-size: 400;
        }
      }
    }

    .menu-btn {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60px;
      height: 60px;
      cursor: pointer;
      transition: all 0.5s ease-in-out;

      .menu-btn_burger {
        width: 50px;
        height: 5px;
        background: var(--heaven);
        border-radius: 5px;
        box-shadow: 0 2px 5px rgba(255, 101, 47, 0.2);
        transition: all 0.4s ease-in-out;

        &::before,
        &::after {
          content: '';
          position: absolute;
          width: 50px;
          height: 5px;
          background: var(--heaven);
          border-radius: 5px;
          box-shadow: 0 2px 5px rgba(255, 101, 47, 0.2);
          transition: all 0.4s ease-in-out;
        }

        &::before {
          transform: translateY(-16px);
        }

        &::after {
          transform: translateY(16px);
        }
      }
      &.open .menu-btn_burger {
        transform: translateX(-50px);
        background: transparent;
        box-shadow: none;

        &::before {
          transform: rotate(45deg) translate(35px, -35px);
        }

        &::after {
          transform: rotate(-45deg) translate(35px, 35px);
        }
      }
    }

    &.sticky {
      position: fixed;
      margin: 0;
      height: 4rem;
      background: var(--heaven);
      color: var(--dark);
      box-shadow: 0 10px 20px var(--shadow);
      transition: 0.5s;
      z-index: 10;

      .menu-btn_burger {
        background-color: var(--dark);

        &::before,
        &::after {
          background-color: var(--dark);
        }
      }

      .desktop {
        li {
          a {
            color: var(--dark);
          }
        }
      }
    }

    ul {
      list-style: none;

      li {
        display: inline-block;
        font-size: 18px;
        padding: 0 1rem;
        color: var(--heaven);
        transition: 0.3s;
        text-decoration: none;
        // cursor: pointer;

        a {
          color: var(--heaven);
        }
      }
    }
  }
}

@media only screen and (max-width: 800px) {
  .header {
    nav {
      padding: 2rem 1rem 1.5rem;
      .desktop {
        display: none;
      }
      .phone {
        display: block;
      }
    }
  }
}

@media only screen and (min-width: 800px) {
  .header {
    nav {
      .desktop {
        display: block;
      }
      .phone {
        display: none;
      }
      .menu-btn {
        display: none;
      }
    }
  }
}
