.area-guide {
  margin: 5rem 6rem;

  .area-guide-list {
    margin: 4rem 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;

    .area-guide-card {
      position: relative;
      box-shadow: 0 10px 25.5px 4.5px rgba(20, 48, 58, 0.2);
      transition: 1s;

      .image {
        position: relative;
        width: 100%;
        height: 22rem;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        transition: 0.5s;

        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          background-color: var(--dark);
          height: 100%;
          width: 100%;
          transform: scale(0.6);
          opacity: 0;
          transition: 0.5s;
        }
      }

      &:hover .image::after {
        transform: scale(1);
        opacity: 0.5;
      }
      .content {
        position: absolute;
        bottom: 19%;
        left: 4%;
        transform: translate(-50% -50%);
        color: var(--heaven);
        opacity: 0;
        transition: 0.5s;
        text-align: left;

        h3 {
          font-size: 24px;
          font-weight: 500;
        }

        h6 {
          font-size: 14px;
          font-weight: 400;

          i {
            margin-right: 5px;
          }
        }
      }

      &:hover .content {
        bottom: 10%;
        opacity: 1;
      }
    }
  }
}

@media only screen and (max-width: 1400px) {
  .area-guide {
    margin: 5rem 2rem;

    .area-guide-list {
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 10px;
    }
  }
}

@media only screen and (max-width: 1000px) {
  .area-guide {
    margin: 5rem 1rem;

    .area-guide-list {
      grid-template-columns: 1fr 1fr;
      grid-gap: 10px;
    }
  }
}

@media only screen and (max-width: 600px) {
  .area-guide {
    margin: 5rem 1rem;

    .area-guide-list {
      grid-template-columns: 1fr;
      grid-gap: 10px;
    }
  }
}
