.our-passion {
  background-image: url(../../assest/images/passion_bg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 250px 6rem 130px;
  position: relative;
  z-index: 1;
  margin-top: -125px;

  .box {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 15px;
    transition: 1s;

    .box-item {
      margin: 2rem 0;
      padding: 30px 20px 35px;
      text-align: center;
      position: relative;
      z-index: 1;

      .icon {
        height: 80px;
        width: 80px;
        display: inline-block;
        text-align: center;
        line-height: 80px;
        border: 1px solid #afb5be;
        border-radius: 5px;
        margin-bottom: 35px;

        .fas {
          font-size: 42px;
          color: var(--heaven);
          display: inline-block;
        }
      }

      h4 {
        font-size: 20px;
        color: var(--heaven);
        font-weight: 600;
        margin-bottom: 25px;
      }

      p {
        color: var(--heaven);
        font-size: 14px;
        margin-bottom: 0;
        line-height: 1.857;
      }

      .button-1 {
        margin-top: 30px;
        position: relative;
        font-weight: 500;
        color: var(--heaven);
        display: inline-block;
        z-index: 4;

        &::after {
          content: '';
          height: 2rem;
          width: 2rem;
          background-color: var(--main);
          position: absolute;
          top: -5px;
          left: -11px;
          border-radius: 50%;
          z-index: -1;
          transition: 0.6s;
        }
      }

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-image: url(../../assest/images/patten_bg.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
        transition: 1s;
      }

      &:hover::after {
        background-color: var(--heaven);
        background-image: none;
      }

      &:hover {
        color: var(--dark);

        .fas {
          color: var(--main);
        }

        h4 {
          color: var(--dark);
        }
        p {
          color: var(--para);
        }
        .our-passon {
          color: var(--dark);
        }
      }
    }
  }

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #1d2e4a;
    opacity: 0.651;
    z-index: -1;
    transition: 1s;
  }
}

@media only screen and (max-width: 1100px) {
  .our-passion {
    margin: 0;
    padding: 250px 0px 130px;

    .rec-arrow {
      font-size: 12px;
    }

    .iLquPE {
      height: 30px;
      width: 20px;
      min-width: 10px;
      line-height: 10px;
      border-radius: 0%;
    }

    .lgksPe {
      height: 30px;
      width: 20px;
      min-width: 10px;
      line-height: 10px;
      border-radius: 0%;
    }
  }

  .box {
    margin: 0;
    padding: 0;
  }
}
