.footer {
  padding: 0 8rem 1rem;
  background-color: var(--dark);
  color: var(--heaven);

  p {
    text-align: center;
    color: #7b7b7b;
  }

  .content-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    margin-bottom: 20px;

    .titles {
      font-size: 18px;
      font-weight: 700;
      color: var(--heaven);
      padding-bottom: 3px;
      display: inline-block;
      border-bottom: 2px solid var(--primary);
      margin: 3rem 0;
      text-transform: uppercase;
    }

    .about-sec {
      .image {
        background-image: url(../../assest/images/footer.jpg);
        background-size: cover;
        background-position: center;
        height: 10rem;
      }
      .logo {
        font-size: 48px;
        font-weight: 400;
        margin: 10px 0;
      }

      p {
        font-size: 14px;
        color: #7d7d7d;
        text-align: left;
      }
    }

    .hours {
      ul {
        list-style: none;
        li {
          display: flex;
          justify-content: space-between;
          font-size: 14px;
          color: #7d7d7d;
          padding: 14px 0;
          border-bottom: 1px solid #7d7d7d;
        }
      }
      h6 {
        font-size: 14px;
        color: #7d7d7d;
        padding: 14px 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        i {
          margin-right: 10px;
          font-size: 24px;
        }
        .fa-phone {
          transform: rotate(90deg);
        }
      }
    }

    .useful-links {
      ul {
        list-style: none;
        li {
          display: flex;
          justify-content: space-between;
          text-align: left;
          font-size: 14px;
          padding: 10px 0;

          span {
            cursor: pointer;
            color: #7d7d7d;
            transition: 0.4s;
            &:hover {
              color: var(--primary);
            }
          }
        }
      }
    }
    .featured-propertier {
      .img-slide {
        background-size: cover;
        background-position: center;
        height: 18rem;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        transition: 1s;
      }

      .img1 {
        background-image: url(../../assest/images/fea-product.jpg);
      }
      .img2 {
        background-image: url(../../assest/images/appat-1.jpg);
      }
      .img3 {
        background-image: url(../../assest/images/appat-2.jpg);
      }
      .img4 {
        background-image: url(../../assest/images/appat-3.jpg);
      }

      .rec-arrow {
        font-size: 12px;
      }

      .iLquPE {
        height: 30px;
        width: 20px;
        min-width: 10px;
        line-height: 10px;
        border-radius: 0%;
      }

      .lgksPe {
        height: 30px;
        width: 20px;
        min-width: 10px;
        line-height: 10px;
        border-radius: 0%;
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .footer {
    padding: 0 1rem 1rem;

    .content-list {
      grid-template-columns: 1fr !important;
      grid-gap: 10px;

      .featured-propertier {
        .img-slide {
          height: 24rem;
        }
      }

      .about-sec {
        .image {
          height: 15rem !important;
        }
      }
    }
  }
}

@media only screen and (max-width: 1100px) {
  .footer {
    padding: 0 1rem 1rem;

    .content-list {
      grid-template-columns: 1fr 1fr;
      grid-gap: 30px;

      .featured-propertier {
        .img-slide {
          height: 20rem;
        }
      }
      .about-sec {
        .image {
          height: 15rem;
        }
      }
    }
  }
}
