.contact-us {
  margin: 5rem 6rem;

  iframe {
    width: 100%;
    margin: 2rem 0;
    height: 30rem;
    position: relative;
    overflow: hidden;
    border: 1px solid #f0e9ff;
    outline: none;
  }

  .contact-form {
    margin: 2rem 0;
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 40px;

    form {
      textarea {
        border: 1px solid #f0e9ff;
        border-radius: 5px;
        min-height: 10rem;
        width: 100%;
        padding: 1rem;
        font-size: 13px;
        background: 0 0;
      }

      .name {
        margin: 1rem 0;
        width: 48%;
        border: 1px solid #f0e9ff;
        border-radius: 5px;
        padding: 1rem;
        font-size: 13px;
        background: 0 0;
      }

      .email {
        margin: 1rem 0;
        float: right;
        width: 48%;
        border: 1px solid #f0e9ff;
        border-radius: 5px;
        padding: 1rem;
        font-size: 13px;
        background: 0 0;
      }

      .subject {
        margin: 1rem 0 2rem;
        width: 100%;
        border: 1px solid #f0e9ff;
        border-radius: 5px;
        padding: 1rem;
        font-size: 13px;
        background: 0 0;
      }
    }

    .info {
      ul {
        list-style: none;

        li {
          display: flex;
          align-items: center;
          margin-bottom: 20px;

          i {
            font-size: 30px;
            margin-right: 10px;
          }

          h5 {
            font-size: 16px;
            font-weight: 500;
            color: var(--dark);
            letter-spacing: 1px;
          }

          h6 {
            font-size: 14px;
            font-weight: 400;
            color: #8a8a8a;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .contact-us {
    margin: 5rem 1rem 1rem !important;
    .contact-form {
      grid-template-columns: 1fr !important;
      grid-gap: 10px;
      form {
        textarea {
          min-height: 6rem;
        }

        .name {
          width: 100%;
        }

        .email {
          width: 100%;
        }
      }
    }

    .info {
      margin-top: 2rem;
    }
  }
}

@media only screen and (max-width: 1100px) {
  .contact-us {
    margin: 5rem 1rem;
    .contact-form {
      grid-template-columns: 2fr 1fr;
      grid-gap: 10px;
      form {
        textarea {
          min-height: 6rem;
        }

        .name {
          width: 100%;
        }

        .email {
          width: 100%;
        }
      }
    }
  }
}
