.read-more-button {
  margin-top: 30px;
  position: relative;
  font-weight: 500;
  color: var(--dark);
  display: inline-block;
  z-index: 4;

  &::after {
    content: '';
    height: 2rem;
    width: 2rem;
    background-color: var(--main);
    position: absolute;
    top: -5px;
    left: -11px;
    border-radius: 50%;
    z-index: -1;
    transition: 0.6s;
  }

  &.our-passon {
    color: var(--heaven);
  }
}
