.background {
  position: relative;
  width: 100%;
  height: 70vh;
  background: url(../../assest/images/background.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--heaven);

  .content {
    text-align: center;

    .page-name {
      font-size: 48px;
    }

    .other-links {
      ul {
        display: flex;
        list-style: none;

        li {
          margin: 1rem;
          font-size: 18px;
          text-align: center;
          a {
            color: var(--heaven);
          }
        }
      }
    }
  }
}
