.happy-customer {
  margin: 100px 0 50px;

  .content {
    position: relative;
    z-index: 2;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: var(--heaven);

    .image {
      height: 9rem;
      width: 9rem;
      border-radius: 50%;
      background-position: center;
      background-size: cover;
      background-image: url(../../assest/images/profile.jpg);
    }
  }

  .desc {
    position: relative;
    z-index: 2;
    text-align: center;
    color: var(--heaven);

    p {
      padding: 1rem 10rem;
    }

    h5 {
      margin: 1rem;
      font-size: 24px;
      font-weight: 700;
    }
  }
}

@media only screen and (max-width: 800px) {
  .happy-customer {
    margin: 100px 0 50px;

    .desc {
      p {
        padding: 1rem 0.5rem;
      }
    }
  }
}
