.slider {
  position: relative;
  width: 100%;
  height: 100vh;
  background: url(../../assest/images/slider.jpg);
  background-size: cover;
  background-position: center;

  .desc {
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-50%, -50%);
    text-align: left;

    p {
      color: #797979;
      font-size: 18px;
      font-weight: 500;
      margin: 2.5rem 0;
    }

    input {
      position: relative;
      height: 3.8rem;
      padding: 20px;
      width: 100%;
      border-radius: 40px;
      border: none;
    }

    button {
      position: absolute;
      top: 90%;
      left: 90%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      float: right;
      border: none;
      background: none;
    }
  }
}

@media only screen and (max-width: 600px) {
  .slider {
    position: relative;
    width: 100%;
    .desc {
      top: 50%;
      left: 50%;
      width: 90%;

      button {
        top: 93% !important;
        left: 90% !important;
      }
    }
  }
}

@media only screen and (max-width: 800px) {
  .slider {
    position: relative;
    width: 100%;
    .desc {
      top: 50%;
      left: 50%;
      width: 90%;

      button {
        top: 90%;
        left: 95%;
      }
    }
  }
}

