.properties {
  margin: 50px 6rem 2rem;

  .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .card-list {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;

    .card {
      padding: 0;
      margin: 0;
      border: none;
      transition: 1.5s;

      .image {
        position: relative;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        height: 13rem;

        .img-footer {
          h6 {
            position: absolute;
            top: 7%;
            left: 5%;
            transform: translate(-50% -50%);
            font-size: 12px;
            font-weight: 400;
            text-transform: uppercase;
            padding: 0.5rem 1rem;
            display: inline-block;
            background: var(--primary);
            color: var(--heaven);
            transition: 0.5s;
          }

          h5 {
            position: absolute;
            top: 75%;
            left: 5%;
            transform: translate(-50% -50%);
            font-size: 18px;
            font-weight: 600;
            text-transform: uppercase;
            padding: 0.5rem 1rem;
            background: var(--heaven);
            display: inline-block;
            color: var(--primary);
            transition: 0.5s;
          }
        }
      }

      .content {
        border: 1px solid #e1dddd;
        padding: 1.4rem 1.4rem 0.5rem;

        h5 {
          font-size: 18px;
          font-weight: 600;
        }

        h6 {
          padding: 0.5rem 0;
          font-size: 14px;
          font-weight: 500;
          color: var(--primary);

          i {
            margin-right: 10px;
          }
        }

        p {
          font-size: 14px;
          color: #797979;
        }
        .footer-card {
          padding: 0.5rem 0.5rem 0;
          ul {
            list-style: none;
            display: flex;
            justify-content: space-between;
            align-items: center;
            li {
              color: var(--primary);
              text-align: left;
              i {
                margin-right: 5px;
              }
            }
          }
        }
      }

      &:hover {
        box-shadow: 0 10px 25.5px 4.5px rgba(20, 48, 58, 0.1);
      }

      &:hover .img-footer h6 {
        background: var(--dark);
        color: var(--heaven);
      }
      &:hover .img-footer h5 {
        background: var(--primary);
        color: var(--heaven);
      }
    }
  }
}

@media only screen and (max-width: 1400px) {
  .properties {
    margin: 50px 2rem 0;

    .card-list {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

@media only screen and (max-width: 1000px) {
  .properties {
    margin: 50px 1rem 0;

    .card-list {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media only screen and (max-width: 600px) {
  .properties {
    margin: 50px 1rem 0;
    .heading {
      display: block;
    }

    .card-list {
      grid-template-columns: 1fr;
    }
  }
}
