.details-page {
  .images {
    margin: 6rem 0;
    .swiper-slide {
      height: 10rem;
      background-position: center;
      background-size: cover;
    }
  }
  .details {
    margin: 6rem 0;
    color: var(--dark);
    h2 {
      font-size: 37px;
      font-weight: 600;
    }
    h4 {
      font-size: 24px;
      font-weight: 600;
      color: var(--primary);
    }
    h6 {
      color: #797979;
      i {
        margin-right: 10px;
      }
    }
    p {
      color: #797979;
    }
    ul {
      list-style: none;
      display: flex;
      justify-content: space-between;
      color: var(--primary);
    }
  }
}

@media only screen and (max-width: 800px) {
  .details-page {
    .images {
      margin: 4rem 0;
    }
    .details {
      margin: 4rem 0;
    }
  }
}
