.bg {
  background-image: url(../../assest/images/happy-bg.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  width: 100%;
  padding: 0 8rem;
  height: 37rem;
  margin: 100px 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--dark);
    width: 100%;
    height: 100%;
    opacity: 0.5;
  }
  .children {
    position: relative;
    z-index: 2;
  }
}

@media only screen and (max-width: 1000px) {
  .bg {
    padding: 0 1rem;
  }
}
