.dream-house {
  position: relative;
  margin: 50px 0 0 0;
  background-image: url(../../assest/images/bg_1.jpg);
  background-position: center;
  background-size: cover;
  text-align: center;
  height: 20rem;
  color: var(--heaven);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  .bg {
    background-color: var(--dark);
    position: absolute;
    opacity: 20%;
    top: 0;
    background-size: cover;
    height: 100%;
    width: 100%;
    z-index: 1;
  }

  .content {
    position: relative;
    z-index: 2;
    h1 {
      font-size: 48px;
      font-weight: 700;
    }
    h6 {
      margin-bottom: 2rem;
    }
  }
}
